* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

body {
	font-family: 'montserrat', sans-serif;
	background-color: #223343;
}

header {
	padding: 50px 15px 0px;
}

header h1 {
	color: #FFF;
	font-size: 48px;
	font-weight: 500;
	text-align: center;
}

main {
	width: 100%;
	max-width: 960px;
	margin: 0 auto;
}

section {
	padding: 15px;
}

.searchbox {
	display: block;
	width: 100%;
	padding: 15px;

	border: none;
	outline: none;
	background: none;

	background-color: #EEE;
	border-radius: 8px;

	color: #53565A;
	font-size: 20px;
	font-weight: 300;

	-webkit-transition: 0.4s ease-out;

	-o-transition: 0.4s ease-out;

	transition: 0.4s ease-out;
}

.searchbox:focus {
	-webkit-box-shadow: 0px 0px 8px 3px #4484C4;
	        box-shadow: 0px 0px 8px 3px #4484C4;
}

.results {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	margin: 0px -15px;
}

.phones {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	margin: 0px -15px;
}

.phones .result {
	width: 33.333%;
	-ms-flex: 1 0 21%;
	    flex: 1 0 21%;
	min-width: 250px;
	padding: 15px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	cursor: pointer;
}


.results .result img {
	display: block;
	width: 100%;
}

.results .result .title {
	color: #FFF;
	font-size: 20px;
	font-weight: 600;
	padding: 10px;
	background: #445565;
	-ms-flex: 1 1 100%;
	    flex: 1 1 100%;
	-webkit-transition: 0.4s ease-out;
	-o-transition: 0.4s ease-out;
	transition: 0.4s ease-out;
}

.results .result:hover .title {
	background: #FFF;
	color: #223343;
}

.popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: #445565;

	overflow-y: scroll;
}

.popup .content {
	display: block;
	width: 100%;
	max-width: 960px;
	margin: 0 auto;
	padding: 25px;

	color: #FFF;
}

.popup .content h2 {
	font-size: 28px;
	font-weight: 600;
}
.popup .content h2 span {
	font-weight: 300;
	font-size: 20px;
}

.popup .content .price {
	margin-bottom: 30px;
}

.popup .content .phoneDetail {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	margin: 0px -15px 30px;
}

.popup .content .phoneDetail img {
	-ms-flex: 1 1 50%;
	    flex: 1 1 50%;
	max-width: 300px;
	min-width: 250px;
	padding: 0px 15px;
}

.popup .content .phoneDetail p {
	-ms-flex: 1 1 50%;
	    flex: 1 1 50%;
	padding: 15px;
	min-width: 200px;
}

.popup .content .close {
	display: inline-block;
	padding: 15px 30px;
	font-size: 18px;
	font-weight: 700;

	background: #4484C4;
	color: #FFF;
	border-radius: 8px;
	border: none;
	outline: none;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	cursor: pointer;

	-webkit-transition: 0.4s ease-out;

	-o-transition: 0.4s ease-out;

	transition: 0.4s ease-out;
}


